/* 메뉴 삭제 버튼 CSS */
del-btn {
  width: 50px;
  height: 30px;
}

/* All CSS */

/* All button CSS */
.buttonArea {
  width: 200px;
  height: 80px;
  float: left;
}

/* button-1 CSS */

.custom-btn {
  color: var(--color);
  transition: 0.25s;
  background: black;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  padding: 1em 2em;
}

.custom-btn:hover,
.custom-btn:focus {
  border-color: var(--hover);
  color: #fff;
}

.button-1 {
  box-shadow: 0.3em 0.3em 0 0 var(--color), inset 0.3em 0.3em 0 0 var(--color);
}
.button-1:hover,
.button-1:focus {
  box-shadow: 0 0 0 0 var(--hover), inset 6em 3.5em 0 0 var(--hover);
}

.button-2:hover,
.button-2:focus {
  box-shadow: inset 0 0 0 2em var(--hover);
}

.button-2:hover,
.button-2:focus {
  box-shadow: inset 0 0 0 2em var(--hover);
}

.button-3:hover,
.button-3:focus {
  animation: button-3 1s;
  box-shadow: 0 0 0 2em transparent;
}

@keyframes button-3 {
  0% {
    box-shadow: 0 0 0 0 var(--hover);
  }
}
.button-4:hover,
.button-4:focus {
  box-shadow: inset -5em 0 0 0 var(--hover), inset 5em 0 0 0 var(--hover);
}

.button-5:hover,
.button-5:focus {
  box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
  transform: translateY(-0.25em);
}

.button-6:hover,
.button-6:focus {
  box-shadow: inset 10em 0 0 0 var(--hover);
}

.button-1 {
  --color: #19bc8b;
  --hover: #1973bc;
}

.button-2 {
  --color: #a972cb;
  --hover: #cb72aa;
}

.button-3 {
  --color: #ef6eae;
  --hover: #ef8f6e;
}

.button-4 {
  --color: #ff7f82;
  --hover: #ffdc7f;
}

.button-5 {
  --color: #ffa260;
  --hover: #e5ff60;
}

.button-6 {
  --color: #8fc866;
  --hover: #66c887;
}

